.fixed-right {
  position: fixed;
  right: 0;
  width: 310px;
  top: auto;
}

.label-notify-count-inner {
  position: absolute;
  text-align: center;
  padding: 3px 4px;
  background-color: #9c9494 !important;
  color: #fff !important;
  /* border: 1px solid transparent; */
  border-radius: 7em 7.5em 7em 7em 7em 7.5em 7em 7.5em;
  min-width: 30px;
  margin-left: 10px;
  margin-top: -2px;
}

.notify-menu {
  .dropdown-menu {
    max-height: 700px;
    padding-top: 0;
    padding-bottom: 0;
    .box-header {
      background-color: #e4e4e4;
      padding: 5px 10px;
    }
    .notify-body {
      max-height: 642px;
      .box-header .material-icons {
        padding: 0;
      }
      .box-body {
        padding-top: 8px;
        padding-bottom: 0;
      }
      .box-body > div {
        padding-left: 0;
        padding-right: 0;
      }
      .collapsed .box-header .arrow-up {
        -webkit-transform: rotate(-180deg);
        -ms-transform: rotate(-180deg);
        -o-transform: rotate(-180deg);
        transform: rotate(-180deg);
      }
    }
    .notify-card {
      vertical-align: middle;
      padding: 6px 12px;
      border-radius: 5px;
      margin: 0 8px 8px;
      .date-outer {
        float: right;
        height: 3px;
        font-size: 10px;
        position: absolute;
        right: 13px;
        top: 4px;
        font-weight: bold;
        color: inherit;
        opacity: 0.8;
      }
      .card-inner-left {
        width: 50px;
        text-align: center;
        font-size: 15px;
        .icon-area .material-icons {
          font-size: 24px;
        }
      }
      .card-inner-right {
        padding-top: 8px;
        position: absolute;
        right: 10px;
        .material-icons {
          font-size: 20px;
        }
        .btn {
          padding-left: 0;
          padding-right: 0;
        }
      }
      .card-inner-center {
        padding-left: 5px;
        width: 232px;
        .notify-msg {
          font-size: 12px;
          color: #9c9494;
        }
      }
      > button.close {
        right: -2px;
        top: 16px;
      }
      .fake-for-vertical-align {
        position: inherit;
        height: 40px;
      }
    }
    .notify-card > div {
      display: inline-block;
      vertical-align: middle;
    }
    .icon-group {
      color: #8a8a8a;
      margin-right: 14px;
      position: absolute;
      right: 0;
      top: -3px;
      .material-icons {
        font-size: 24px;
      }
    }
  }
}
    
